import no1 from'./components/no1.vue'
import lee from'./components/lee.vue'
import lun from'./components/lun.vue'
import cun from'./view/cun.vue'
import index from'./view/index.vue'
let publics={
    install(Vue){
        Vue.component('no1',no1)
        Vue.component('lee',lee)
        Vue.component('lun',lun)
        Vue.component('cun',cun)
        Vue.component('index',index)
    }
}
export default publics