import Vue from 'vue'
import Router from'vue-router'
import index from'../view/index.vue'
import ern from'../view/ern.vue'
import ernn from'../view/ernn.vue'
import ernz from'../view/ernz.vue'
import erns from'../view/erns.vue'
import ernd from'../view/ernd.vue'
import wen from'../view/wen.vue'
import men from'../view/men.vue'
import xin from'../view/xin.vue'
import dtu from'../view/dtu.vue'
import wenn from'../view/wenn.vue'
import wene from'../view/wene.vue'
import wenh from'../view/wenh.vue'
import trny from'../view/trny.vue'
import trnn from'../view/trnn.vue'
import xiang from'../view/xiang.vue'
import xie from'../view/xie.vue'
Vue.use(Router)
let routes=[
    {path:'/',name:'index',component:index},
    {path:'/ern',name:'ern',redirect:'/ern/ernz',component:ern,children:[{path:'ernz',name:'ernz',component:ernz},{path:'ernn',name:'ernn',component:ernn},{path:'erns',name:'erns',component:erns},{path:'ernd',name:'ernd',component:ernd}]}, 
    {path:'/wen',name:'wen',redirect:'/wen/wenn',component:wen,children:[{path:'wenn',name:'wenn',component:wenn},{path:'wene',name:'wene',component:wene},{path:'wenh',name:'wenh',component:wenh}]},
    {path:'/men',name:'men',component:men},
    {path:'/xin',name:'xin',component:xin},
    {path:'/dtu',name:'dtu',component:dtu},
    {path:'/trny',name:'trny',component:trny},
    {path:'/trnn',name:'trny',component:trnn},
    {path:'/xiang',name:'xiang',component:xiang},
    {path:'/xie',name:'xie',component:xie},
]
export default new Router({
    // mode:"history",
    routes:routes,
    // redirect:"/ern/cernz"重定向
})